<script>
import {useToast} from "vue-toastification";

export default {
  name: "password_reset_confirm",
  data(){
    return {
      password1: null,
      password2: null,
      passErrors:null,
      errors: {
        new_password1: [],
        new_password2: [],
      },
  }
  },
  methods:{
    reset(){
      const params = {
        'new_password1': this.password1,
        'new_password2': this.password2
      }
      const Rparams = this.$route.params;
      const url = `/reset-password/confirm/${Rparams['uidb64']}/${Rparams['token']}/`;
      console.log(url)
      this.axios.post(url, {params: params}, {params: params}).then((response) => {
        console.log(response)
        console.log(response.data['message'])
        console.log(response.data['data'])
        if (response.data['message']=='password_reset_successful'){
          this.forget=false;
          this.changePassword=false
           const toast = useToast();
           toast.success("رمزعبور با موفقیت تغییر یافت");
        }
        if (response.data['message']=='password_reset_errors'){
          const toast = useToast();
           toast.error("خطا در تغییر رمز عبور!");
           this.passErrors = JSON.parse(response.data['data'])
           console.log(this.passErrors)
          // Map backend errors to form fields
          for (const field in this.passErrors) {
            console.log(field)
            if (this.errors.hasOwnProperty(field)) {
              this.errors[field] = this.passErrors[field].map(e => e.message);
            }
          }
        }
      })

    },
  },
  mounted(){
    console.log('pass reset confirm')
    console.log(this.$route.params)
    // console.log(this.$route.query)
  }
}
</script>

<template>
  <div>
    <teleport to="body">
      <section class="fixed inset-0 bg-gray-50 dark:bg-gray-900 z-50 overflow-y-auto">
        <div class="flex flex-col items-center justify-center px-6 py-8 mx-auto md:h-screen overflow-y-auto lg:py-0">
            <div class="w-full bg-white rounded-lg shadow dark:border md:mt-0 sm:max-w-md xl:p-0 dark:bg-gray-800 dark:border-gray-700">
                <div class="p-6 space-y-4 md:space-y-6 sm:p-8">
                    <h1 class="text-xl font-bold leading-tight tracking-tight text-gray-900 md:text-2xl dark:text-white">
                      بازیابی رمز عبور
                    </h1>
                    <div class="space-y-4 md:space-y-6">
                        <div class="space-y-4 md:space-y-6">
                          <label class="block mb-2 text-sm font-medium text-gray-900 dark:text-white">رمز عبور جدید</label>
                          <input v-model="password1" type="password" placeholder="••••••••" class="bg-gray-50 border border-gray-300 text-gray-900 rounded-lg focus:ring-primary-600 focus:border-primary-600 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" required="">
                          <ul class="text-sm text-gray-900">
                            <li>رمز عبور شما نباید خیلی شبیه به سایر اطلاعات شخصی شما باشد.</li>
                            <li>رمز عبور شما باید حداقل ۸ کاراکتر داشته باشد.</li>
                            <li>رمز عبور شما نباید یک رمز عبور معمولاً استفاده شده باشد.</li>
                            <li>رمز عبور شما نباید کاملاً عددی باشد.</li>
                          </ul>
                          <div v-if="errors.new_password1" class="invalid-feedback">
                            <p v-for="(error, index) in errors.new_password1" :key="index" class="text-sm text-red-900">>{{ error }}</p>
                          </div>
                          <label class="block mb-2 text-sm font-medium text-gray-900 dark:text-white">تکرار رمز عبور</label>
                          <input v-model="password2" type="password" placeholder="••••••••" class="bg-gray-50 border border-gray-300 text-gray-900 rounded-lg focus:ring-primary-600 focus:border-primary-600 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" required="">
<!--                          <p class="text-sm text-gray-900">کد ارسال شده از طرق ربات به اکات تلگرامتان را وارد کنید.</p>-->
                          <div v-if="errors.new_password2" class="invalid-feedback">
                            <p v-for="(error, index) in errors.new_password2" :key="index" class="text-sm text-red-900">>{{ error }}</p>
                          </div>
                          <button @click="reset" class="w-full text-white bg-green-600 hover:bg-green-700 focus:ring-4 focus:outline-none focus:ring-green-300 font-medium rounded-lg text-sm px-5 py-2.5 text-center dark:bg-primary-600 dark:hover:bg-primary-700 dark:focus:ring-blue-800">بازیابی</button>
                        </div>
                    </div>
                  <hr class="h-px my-8 bg-gray-200 border-0 dark:bg-gray-700">
                    <h1 @click="this.$router.push('/Login');" class="text-red-600 font-medium text-sm hover:text-red-800 hover:cursor-pointer">بازگشت به صفحه لاگین</h1>
                </div>
            </div>
        </div>
      </section>
    </teleport>
  </div>
</template>