<script>
import {useToast} from "vue-toastification";

export default {
  name: "passwordReset",
  data(){
    return {
      email: null,
      valid: false
    }
  },
  methods:{
    passwordResetRequest(){
      const params = {
        'email': this.email,
      }
      this.axios.post('/passwordResetRequest/', {params: params}, {params: params}).then((response) => {
        console.log(response)
        console.log(response.data['message'])
        console.log(response.data['data'])

        if (response.data['message']=='password_reset_confirm'){
          this.changePassword = true;
           const toast = useToast();
           this.valid = true
           toast.success("ایمیل معتبر است!");
        }
        if (response.data['message']=='user_not_found'){
          this.changePassword = true;
           const toast = useToast();
           toast.error("کاربری با این ایمیل  در سیستم یافت نشد!");
        }
        if (response.data['message']=='invalid_email'){
          this.changePassword = true;
           const toast = useToast();
           toast.error("ایمیل معتبر نیست!");
        }
      })
    },
  },
}
</script>

<template>
  <div>
    <teleport to="body">
      <section class="fixed inset-0 bg-gray-50 dark:bg-gray-900 z-50 overflow-y-auto">
        <div class="flex flex-col items-center justify-center px-6 py-8 mx-auto md:h-screen overflow-y-auto lg:py-0">
            <div class="w-full bg-white rounded-lg shadow dark:border md:mt-0 sm:max-w-md xl:p-0 dark:bg-gray-800 dark:border-gray-700">
                <div class="p-6 space-y-4 md:space-y-6 sm:p-8">
                    <h1 class="text-xl font-bold leading-tight tracking-tight text-gray-900 md:text-2xl dark:text-white">
                      بازیابی رمز عبور
                    </h1>
                    <div class="space-y-4 md:space-y-6">
                        <div class="space-y-4 md:space-y-6">
                          <label class="block mb-2 text-sm font-medium text-gray-900 dark:text-white">ایمیل</label>
                          <input v-model="email" type="email" name="email" autocomplete="email" maxlength="254" aria-invalid="true"  class="bg-gray-50 border border-gray-300 text-gray-900 rounded-lg focus:ring-primary-600 focus:border-primary-600 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" placeholder="ایمیل خودرا وارد کنید ...">
                          <p v-if='valid' class="block text-sm font-medium text-green-600">لینک بازیابی رمز عبور به ایمیل شما ارسال شد.</p>
                          <button @click="passwordResetRequest" class="w-full text-white bg-green-600 hover:bg-green-700 focus:ring-4 focus:outline-none focus:ring-green-300 font-medium rounded-lg text-sm px-5 py-2.5 text-center dark:bg-primary-600 dark:hover:bg-primary-700 dark:focus:ring-blue-800">تایید</button>
                        </div>
                    </div>
                  <hr class="h-px my-8 bg-gray-200 border-0 dark:bg-gray-700">
                  <h1 @click="this.$router.push('/Login')" class="text-red-600 font-medium text-sm hover:text-red-800 hover:cursor-pointer">بازگشت به صفحه لاگین</h1>
                </div>
            </div>
        </div>
      </section>
    </teleport>
  </div>
</template>