<script>
import {useToast} from "vue-toastification";

export default {
  name: "paymentMessage",
  data() {
    return {
      error: false,
    }
  },
  created() {
    // Alternative: Pure JS (if not using Vue Router)
    const urlParams = new URLSearchParams(window.location.search);
    this.authority = urlParams.get('Authority');
    this.status = urlParams.get('Status');
    console.log('created')
    console.log(this.authority, this.status)
    if(this.status == "OK")
      {
          // post query to server
        this.postQueryData();
      }
  },
  methods: {
    postQueryData() {
      this.axios.post('/paymentMessage/', {
        Authority: this.authority,
        Status: this.status
      },
      {
        params: {
        Authority: this.authority,
        Status: this.status}
      })
      .then(response => {
        console.log('Server response:', response.data);
        // Handle successful response here
        const error = response.data['error']
        console.log(error)
        if(error){
          this.error = error
        }
      })
      .catch(error => {
        console.error('Error posting data:', error);
        // Handle error response here
      });
    }
  }
}
</script>

